<script>
  export let checked = false;
  export let color = false;
  export let label = false;
</script>

<style>
  .container {
    display: flex;
    align-items: center;
  }
  .label {
    font-size: 1.1rem;
    margin: 0 0.5em;
  }
  .switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
  }

  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }

  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: 0.4s;
    transition: 0.4s;
    border-radius: 34px;
  }

  .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
    border-radius: 50%;
  }

  input:checked + .slider {
    background-color: var(--color-secondary);
  }

  input:checked + .slider {
    box-shadow: 0 0 1px var(--color-secondary);
  }

  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
  }
</style>

<div class="container">
  {#if label}
    <div class="label">{label}</div>
  {/if}
  <label class="switch" style={color ? `--color-secondary: ${color}` : ``}>
    <input type="checkbox" bind:checked />
    <span class="slider" />
  </label>
</div>
